.projects-container {
    padding: 20px;
  }
  
  .project-item {
    margin-bottom: 20px;
  }
  
  .project-title {
    font-size: 1.5em;
  }
  
  .project-date {
    font-size: 1em;
  }
  
  .project-description {
    font-size: 1em;
  }
  
  .project-details {
    list-style-type: disc;
    margin-left: 20px;
  }
  
  .project-achievements {
    font-style: italic;
  }
  
  /* Responsive Layout */
  @media (max-width: 768px) {
    .projects-container {
      padding: 10px;
    }
  
    .project-title {
      font-size: 1.2em;
    }
  
    .project-date, .project-description, .project-achievements {
      font-size: 0.9em;
    }
  
    .project-details {
      margin-left: 15px;
    }
  }
  