.about-container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    font-family: 'Arial', sans-serif;
    line-height: 1.6;
  }
  
  .about-title {
    font-size: 2.5em;
    margin-bottom: 20px;
    color: #2c3e50;
    text-align: center;
  }
  
  .about-subtitle {
    font-size: 1.8em;
    margin-top: 20px;
    color: #34495e;
  }
  
  .about-text {
    font-size: 1.2em;
    margin-bottom: 15px;
    color: #555;
  }
  
  .about-list {
    list-style-type: decimal;
    margin-left: 20px;
  }
  
  .about-list-item {
    font-size: 1.1em;
    margin-bottom: 20px;
    color: #333;
  }
  
  .about-list-item h3 {
    font-size: 1.5em;
    margin-bottom: 10px;
    color: #2c3e50;
  }
  
  .about-container p,
  .about-container li {
    max-width: 750px;
  }
  