.experience-container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    font-family: 'Arial', sans-serif;
    line-height: 1.6;
  }
  
  .experience-title {
    font-size: 2.5em;
    margin-bottom: 20px;
    color: #2c3e50;
    text-align: center;
  }
  
  .experience-subtitle {
    font-size: 1.8em;
    margin-top: 20px;
    color: #34495e;
  }
  
  .experience-date {
    font-size: 1.1em;
    margin-bottom: 10px;
    color: #555;
  }
  
  .experience-description {
    font-size: 1.2em;
    margin-bottom: 15px;
    color: #555;
  }
  
  .experience-list {
    list-style-type: disc;
    margin-left: 20px;
  }
  
  .experience-list li {
    font-size: 1.1em;
    margin-bottom: 10px;
    color: #333;
  }
  
  .project-title {
    font-size: 1.5em;
    margin-top: 20px;
    color: #2c3e50;
  }
  
  .project-date {
    font-size: 1.1em;
    margin-bottom: 10px;
    color: #555;
  }
  
  .project-description {
    font-size: 1.2em;
    margin-bottom: 15px;
    color: #555;
  }
  
  .project-list {
    list-style-type: disc;
    margin-left: 20px;
  }
  
  .project-list li {
    font-size: 1.1em;
    margin-bottom: 10px;
    color: #333;
  }
  