body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

nav {
  background-color: #333;
  overflow: hidden;
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
}

nav li {
  float: left;
}

nav li a {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

nav li a:hover {
  background-color: #444;
}

nav li a.active {
  background-color: #444;
}

/* Responsive design */
@media screen and (max-width: 600px) {
  nav ul {
    flex-direction: column;
  }
  
  nav li {
    float: none;
  }
  
  nav li a {
    border-bottom: 1px solid #555;
  }
}